.cell-default {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border-style: solid;
  border-color: gainsboro;
  background-color: beige;
}

.cell-default.active-0 {
  background-color: #FBF35E;
}

.cell-default.active-1 {
  background-color: #F6F5BD;
}

.cell-banner {
  margin: 0;
  padding: 0;
  will-change: opacity;
  box-sizing: border-box;
  border-style: solid;
  border-color: darkslategray;
  background-color: black;
  transition: all 0.8s ease-in-out;
}

.cell-banner.active-0 {
  background-color: black;
}

.cell-banner.active-1 {
  background-color: black;
}

.cell-banner.shrink {
  background-color: mediumseagreen;
  border: none;
  border-radius: 50%;
  transform: scale(0.7);
}



@keyframes fadein {
  from {
    opacity: 0.4;
  }

  to {
    opacity: 1.0;
  }
}